/* ------------------------------------------------------------------------------------------------ */
/* Content */
/* ------------------------------------------------------------------------------------------------ */

// Login page

.login-page {
  height: calc(100vh - 78px);
  overflow: hidden;
  position: relative;
  .container {
    height: 100%; }
  .row {
    height: 100%; }
  &__wrap {
    width: 100%;
    h2 {
      color: $secondary; }
    a {
      font-size: 14px;
      line-height: 1.5em;
      color: $primary;
      font-weight: 600;
      text-decoration: none;
      &:hover {
        opacity: .8; } } }
  .col-lg-5 {
    display: flex;
    align-items: center; }
  .col-lg-7 {
    position: relative; }
  &__image {
    width: 50vw;
    height: 100vh;
    max-width: 803px;
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 40px 0 0 0;
    img {
      border-radius: 40px 0 0 0;
      width: 100%;
      height: 100%;
      object-fit: cover; } }

  .form-footer {
    .btn {
      display: block;
      width: 100%; } }

  .rememberme {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    .lostpasswrd {} }

  .register-now {
    text-align: center;
    margin-top: 30px;
    a {
      text-decoration: none; }
    p {
      color: #838683; } } }


// Sidebar
.sidebar {
  width: 250px;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  &__logo {
    display: inline-block;
    vertical-align: middle;
    padding: 24px;
    margin-bottom: 20px;
    color: $text;
    text-decoration: none;
    &:hover {
      text-decoration: none; }
    img {
      max-height: 34px;
      width: auto;
      display: inline-block;
      vertical-align: middle;
      margin-right: 12px; }
    span {
      font-size: 20px;
      line-height: 1.2em;
      font-weight: 700;
      color: $text;
      text-decoration: none; } }

  .sidemenu {
    background: #FFF;
    li {
      a {
        display: block;
        padding: 20px 24px;
        color: $text;
        text-decoration: none; }
      &:hover {
        a {
          color: $secondary;
          background: #F1F7ED; } }
      &.current-menu-item {
        a {
          color: $secondary;
          background: #F1F7ED;
          font-weight: 600; } } } } }

.drop-box {
  display: block;
  width: 100%;
  text-align: center;
  border: 2px dashed #E7E9F0;
  background: #F4F5F6;
  border-radius: 4px;
  background-image: url(../images/icon-picture.svg);
  background-size: 118px 100px;
  background-position: center 75px;
  background-repeat: no-repeat;
  position: relative;
  padding: 40px;
  .ic-upload {
    width: 42px;
    height: 42px;
    margin: 0 auto 20px; }
  .inputfile {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    transition: all 0.3s;
    outline: none;
    opacity: 0; }
  .box-btn {
    a {
      color: $primary; }
    span {
      font-size: 12px;
      line-height: 14px;
      color: #ACB3C0; } } }

.modal-dialog {
  .modal-title {
    h3 {
      font-size: 16px;
      line-height: 24px; } }
  h4 {
    font-size: 16px;
    line-height: 24px;
    font-weight: 600; }
  .form-group {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0; }
    label {
      font-size: 13px;
      line-height: 20px;
      display: block; }
    &.tgl {
      .row {
        margin-left: -5px;
        margin-right: -5px; }
      [class*="col-"] {
        padding-left: 5px;
        padding-right: 5px; } }
    small {
      font-size: 11px;
      line-height: 13px;
      color: #838683;
      font-weight: 400; } }
  .cstm-btnradio {
    .form-check-inline {
      position: relative;
      &:last-child {
        margin-right: 0; } }
    .form-check-label {
      padding: 13px 16px;
      border: 1px solid #BDBFBD;
      border-radius: 10px;
      position: relative;
      min-width: 248px;
      cursor: pointer; }
    .form-check-input {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      visibility: hidden; }
    .form-check-input:checked~.form-check-label {
      background: #E3F0DA;
      border-color: #75B248;
      color: #75B248; } }
  .bootstrap-select.select {
    width: 100% !important; }

  .form-action {
    padding-top: 16px;
    .btn-link {
      color: #838683; } }

  .box-form-materi {
    padding: 20px 16px;
    border: 1px solid $border;
    border-radius: 20px;
    &:not(:last-child) {
      margin-bottom: 20px; }
    h4 {
      font-size: 13px;
      line-height: 20px;
      position: relative; }
    .delete-materi {
      position: absolute;
      top: 0;
      right: 0; } }

  .addmore {
    text-align: center;
    a {
      display: inline-block;
      vertical-align: middle;
      color: #DC7702;
      font-size: 14px;
      line-height: 16px;
      .ic {
        width: 18px;
        height: 18px;
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
        img {
          display: block;
          width: 100%;
          height: 100%; } } } }

  .upload-pp {
    .drop-box {
      padding: 0;
      border: 0;
      background: transparent;
      .image-preview {
        width: 94px;
        height: 94px;
        margin: 0 auto 20px;
        border-radius: 50%;
        overflow: hidden;
        background: url(../images/pp-default.png);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center; }
      label {
        color: $primary;
        font-size: 14px;
        line-height: 16px;
        font-weight: 500; } } } }


