/* General */
/* ------------------------------------------------------------------------------------------------ */

.btn {
  font-size: 14px;
  line-height: 1.2em;
  padding: 14px 24px;
  border-radius: 10px;
  font-weight: 500;
  transition: all .3s ease;
  &:hover {
    text-decoration: none; }
  &:focus, &.focus {
    outline: 0;
    box-shadow: none; }
  &.disabled,
  &:disabled {
    opacity: 0.5; }
  .icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    &:first-child {
      margin-right: 8px; }
    &:last-child {
      margin-left: 8px; } }
  span {
    display: inline-block; }
  // BTN PRIMARY
  &-primary {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:hover {
    color: #FFF;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:focus, &-primary.focus {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%);
    box-shadow: none; }
  &-primary.disabled, &-primary:disabled {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:not(:disabled):not(.disabled):active,
  &-primary:not(:disabled):not(.disabled).active,
  .show > &-primary.dropdown-toggle {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:not(:disabled):not(.disabled).active:focus,
  &-primary:not(:disabled):not(.disabled):active:focus,
  .show > &-primary.dropdown-toggle:focus {
    box-shadow: none; }

  // BTN Link
  &-link {
    color: $primary; }
  &-link:hover {
    color: $primary;
    opacity: .8; } }


.admin-content {
  padding: 50px 38px;
  background: #F2F2F1;
  margin-left: 250px;
  width: calc(100% - 250px);
  min-height: calc(100vh - 80px);
  overflow: hidden; }

.page-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
  h2 {
    margin-bottom: 0; } }

.section {
  & + .section {
    margin-top: 50px; } }

.section-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.75rem;
  h3 {
    margin-bottom: 0; }
  p {
    font-size: 14px;
    line-height: 22px;
    color: #838683; }
  .right {
    display: flex; }
  .select {
    margin-right: 20px; }
  .search-box {
    position: relative;
    width: 216px;
    &:before {
      content: "";
      width: 20px;
      height: 20px;
      position: absolute;
      top: 9px;
      left: 12px;
      background-image: url(../images/ic-search.svg);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center; }
    .form-control {
      background-color: #FFF;
      padding-left: 46px; } } }

// Box Card
.box-card {
  width: 100%;
  background: #FFF;
  border-radius: 20px;
  border: 1px solid rgba(48, 121, 49, 0.2);
  &__img {
    width: 100%;
    position: relative;
    border-radius: 20px 20px 0 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }
  &__text {
    padding: 20px 16px;
    span.tag {
      font-size: 12px;
      line-height: 14px;
      padding: 5px 6px;
      background: $light;
      color: #6C841D;
      border-radius: 8px;
      margin-bottom: 16px;
      display: inline-block;
      vertical-align: middle;
      &-scndry {
        color: #278D68;
        background: #278D6824; } }

    h4 {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 14px;
      font-weight: 600;
      a {
        color: $text; } }
    .schedule {
      margin-bottom: 24px;
      ul {
        li {
          margin-bottom: 16px;
          &:last-child {
            margin-bottom: 0; } } }
      .ic {
        display: inline-block;
        vertical-align: middle;
        width: 16px;
        height: 16px;
        margin-right: 8px;
        img {
          width: 100%;
          height: 100%; } }
      span {
        display: inline-block;
        vertical-align: middle;
        font-size: 13px;
        line-height: 20px; } }

    .author {
      .pp {
        display: inline-block;
        vertical-align: middle;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        margin-right: 15px; }
      span {
        font-size: 13px;
        line-height: 20px; } } }

  &__footer {
    padding: 20px 16px;
    padding-top: 0;
    display: flex;
    justify-content: space-between;
    .price {
      font-weight: 600;
      color: $secondary; } }
  .action {
    position: relative;
    .dot {
      width: 24px;
      height: 24px;
      cursor: pointer; }
    .action-sub {
      position: absolute;
      bottom: 90%;
      left: 50%;
      padding: 20px;
      border-radius: 24px;
      background: #FFF;
      box-shadow: 0px 20px 44px rgba(0, 0, 0, 0.1);
      min-width: 130px;
      transition: all .3s ease-in-out;
      opacity: 0;
      visibility: hidden;
      z-index: 5;
      ul {
        li {
          font-size: 14px;
          line-height: 22px;
          margin-bottom: 20px;
          &:last-child {
            margin-bottom: 0; }
          a {
            color: $text; }
          &.dlt {
            a {
              color: #E22424; } } } } }

    &:hover {
      .action-sub {
        bottom: 100%;
        opacity: 1;
        visibility: visible; } } } }

.wrap-box-card {
  [class^='col-md'] {
    &:last-child {
      .box-card {
        &__footer {}
        .action {
          .action-sub {
            left: auto;
            right: 50%; } } } } } }


// Box Pemateri
.cards-pemateri {}
.card-pemateri {
  position: relative;
  padding: 20px;
  margin-bottom: 24px;
  .box-card {
    &__photo {
      width: 70px;
      height: 70px;
      margin-bottom: 24px;
      img {
        width: 100%;
        height: 100%; } }
    &__namepos {
      h4 {
        font-size: 16px;
        line-height: 24px;
        font-weight: 600;
        margin-bottom: 8px;
        a {
          color: $text; } }
      .pos {
        font-size: 14px;
        line-height: 20px;
        color: #838683; } } }
  .action {
    position: absolute;
    top: 20px;
    right: 20px;
    .action-sub {
      border: 1px solid #BDBFBD;
      box-sizing: border-box;
      border-radius: 10px;
      padding: 10px;
      min-width: 95px;
      bottom: auto;
      top: 90%;
      ul {
        li:not(:last-child) {
          padding-bottom: 10px;
          margin-bottom: 10px;
          border-bottom: 1px solid #BDBFBD; } } }
    &:hover {
      .action-sub {
        bottom: auto;
        top: 100%; } } } }


.benefits-table {
  background: #FFF;
  padding: 20px;
  border-radius: 8px;
  .table {
    thead {
      border-bottom: 0;
      th {
        background: $light;
        border: 0;
        font-size: 14px;
        line-height: 22px;
        font-weight: 600;
        color: $secondary;
        &:first-child {
          border-radius: 8px 0 0 8px; }
        &:last-child {
          border-radius: 0 8px 8px 0; } } }
    p {
      margin-bottom: 0; }
    tbody {
      td {
        border-top: 0;
        border-bottom: 0.5px solid #D1D9E1;
        vertical-align: middle;
        small {
          font-size: 13px;
          line-height: 20px;
          color: #838683; }
        a {
          color:  $text;
          text-decoration: none; } } }
    .action {
      a {
        display: inline-block;
        vertical-align: middle;
        width: 20px;
        height: 20px;
        & + a {
          margin-left: 20px; } } }
    .total-class {
      text-align: right; }
    td.total-class {
      span {
        padding: 2px 12px;
        background: $primary;
        border-radius: 11px;
        color: #FFF;
        font-weight: 600;
        text-align: center; } } }

  .pagination-box {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    align-items: center;
    .pagination {
      margin-left: -8px;
      margin-right: -8px;
      margin-right: 24px;
      li {
        display: inline-block;
        vertical-align: middle;
        padding-left: 8px;
        padding-right: 8px;
        a {
          display: inline-block;
          vertical-align: middle;
          width: 24px;
          height: 24px;
          font-size: 14px;
          line-height: 24px;
          background: #fbf1e6;
          color: $primary;
          border-radius: 50%;
          text-align: center;
          text-decoration: none; }
        &.prev,
        &.next {
          a {
            text-align: center;
            background: transparent; }
          .svg {
            display: inline-block;
            width: 24px;
            height: 24px;
            margin: 0 auto; }
          &:hover {
            a {
              background: transparent; } } }
        &.next {
          .svg {
            transform: rotate(180deg); } }
        &:hover,
        &.active {
          a {
            background: $primary;
            color: #FFF; } } } }
    .select {
      max-width: 120px;
      .btn {} } } }

.loadmore {
  margin-top: 24px;
  .btn {
    font-weight: 500; } }
