/* ------------------------------------------------------------------------------------------------ */
/* Footer */
/* ------------------------------------------------------------------------------------------------ */
.footer {
  background: #fff;
  padding: 16px 38px;
  margin-left: 250px;
  width: calc(100% - 250px);
  &__main {
    .container {
      width: 100%;
      max-width: 100%;
      .row {
        align-items: center; } }
    .cpyright {
      font-size: 14px;
      line-height: 1.5em;
      color: $text;
      opacity: .8; }
    span {
      font-size: 14px;
      line-height: 1.5em;
      color: $text;
      font-weight: 600; }
    [class^='col-md'] {
      &:last-child {
        text-align: right; } } } }
