/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

.header {
  padding: 12px 0;
  background: #FFF;
  max-height: 80px;
  .row {
    align-items: center; }
  &__logo {
    display: block;
    max-height: 54px;
    width: auto;
    img {
      max-width: 100%;
      max-height: 54px; } }
  &__right {
    text-align: right;
    .play-music {
      display: inline-block;
      vertical-align: middle;
      margin-right: 30px;
      span {
        font-size: 14px;
        line-height: 1.5em; } }
    .language {
      display: inline-block;
      vertical-align: middle;
      a {
        display: inline-block;
        vertical-align: middle;
        color: $text-light;
        text-decoration: none;
        &.active {
          color: $text; } }
      span {
        display: inline-block;
        vertical-align: middle;
        width: 1px;
        height: 17px;
        background: #313343;
        margin: 0 10px; } } } }

.switch {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  height: 14px;
  margin-left: 16px;
  input {
    opacity: 0;
    width: 0;
    height: 0; }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #BDBFBD;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 20px;
    border: 1px solid #BDBFBD;
    &:before {
      position: absolute;
      content: "";
      height: 20px;
      width: 20px;
      left: -4px;
      bottom: -4px;
      background: #FFF;
      border: 2px solid #BDBFBD;
      border-radius: 50%;
      -webkit-transition: .4s;
      transition: .4s; } }
  input {
    &:checked + .slider {
      background: #BDBFBD; }
    &:focus + .slider {
      box-shadow: 0 0 1px #2196F3; }
    &:checked + .slider:before {
      -webkit-transform: translateX(26px);
      -ms-transform: translateX(26px);
      transform: translateX(26px); } } }
