/* ------------------------------------------------------------------------------------------------ */
/* Custom Plugins */
/* ------------------------------------------------------------------------------------------------ */

.owl-carousel {
  position: relative;
  * {
    outline: none !important; }
  .owl-nav {
    position: absolute;
    z-index: 2;
    width: 100%;
    top: 50%;
    span {
      display: block;
      padding: 6px;
      opacity: 1;
      border-radius: 21px;
      border: 1px solid transparent;
      transition: all .3s ease;
      img {
        display: block;
        width: 30px;
        height: 30px; }
      &:hover {
        border: 1px solid #f1f1f1;
        background: #FFF;
        box-shadow: 2px 2px 8px #00000020; } }
    button.disabled {
      opacity: 0.5;
      pointer-events: none; }
    button.owl-next {
      position: absolute;
      top: -21px;
      right: -54px; }
    button.owl-prev {
      position: absolute;
      top: -21px;
      left: -54px; } }
  .owl-dots {
    display: block;
    padding-top: 16px;
    font-size: 0;
    line-height: 0;
    text-align: center; }
  button.owl-dot {
    display: inline-block;
    span {
      display: block;
      width: 30px;
      height: 6px;
      background: #E6E6E6;
      border-radius: 3px;
      margin: 0 3px; }
    &.active span {
      background: #000; } } }



// modal
.modal-open {
  overflow: hidden;
  .modal {
    overflow-x: hidden;
    overflow-y: auto; } }


.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0; }

.modal-dialog {
  position: relative;
  width: auto;
  margin: 0.5rem;
  pointer-events: none; }

.modal.fade .modal-dialog {
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  -webkit-transform: scale(0.3);
  transform: scale(0.3); }

.modal.show .modal-dialog {
  transform: none; }

.modal.modal--static .modal-dialog {
  -webkit-transform: scale(1.02);
  transform: scale(1.02); }

.modal-dialog--scrollable {
  display: -ms-flexbox;
  display: flex;
  max-height: calc(100% - 1rem); }

.modal-dialog--scrollable .modal-content {
  max-height: calc(100vh - 1rem);
  overflow: hidden; }

.modal-dialog--scrollable .modal-header,
.modal-dialog--scrollable .modal-footer {
  -ms-flex-negative: 0;
  flex-shrink: 0; }

.modal-dialog--scrollable .modal-body {
  overflow-y: auto; }

.modal-dialog--centered {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  min-height: calc(100% - 1rem); }

.modal-dialog--centered::before {
  display: block;
  height: calc(100vh - 1rem);
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  content: ""; }

.modal-dialog--centered.modal-dialog--scrollable {
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  height: 100%; }

.modal-dialog--centered.modal-dialog--scrollable .modal-content {
  max-height: none; }

.modal-dialog--centered.modal-dialog--scrollable::before {
  content: none; }

.modal-dialog.modal-dialog--sm {
  max-width: 320px; }

.modal-content {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #ffffff;
  background-clip: padding-box;
  border-radius: 1.5rem;
  outline: 0;

  h2 {
    margin-bottom: 2.5rem; }

  .modal-title {
 } }    // background: #F2F4F7

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  &.fade {
    opacity: 0; }
  &.show {
    opacity: .75; } }

.modal-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
  align-items: flex-start;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 1rem 1rem;
  border-bottom: 1px solid #dee2e6;
  border-top-left-radius: calc(0.5rem - 1px);
  border-top-right-radius: calc(0.5rem - 1px); }
.close {
  width: 24px;
  height: 24px;
  display: inline-block;
  position: absolute;
  top: 32px;
  right: 32px; }

.modal-title {
  margin-bottom: 0;
  margin-bottom: .5rem;
  border-radius: 24px 24px 0 0;
  h2 {
    margin-bottom: 0;
    font-size: 0.875rem;
    liine-height: 1.5em; } }

.modal-body {
  position: relative;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  padding: 2rem;
  // padding-top: 0
  p {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 0.5rem;
    + .form-action {
      margin-top: 1.5rem; } } }

.modal-footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: end;
  justify-content: flex-end;
  padding: 0.75rem;
  border-top: 1px solid $border;
  border-bottom-right-radius: calc(0.5rem - 1px);
  border-bottom-left-radius: calc(0.5rem - 1px); }

.modal-footer > * {
  margin: 0.25rem; }

.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll; }




@media (min-width: 576px) {
  .modal-dialog {
    max-width: 580px;
    margin: 1.75rem auto; }
  .modal-dialog--scrollable {
    max-height: calc(100% - 3.5rem); }
  .modal-dialog--scrollable .modal-content {
    max-height: calc(100vh - 3.5rem); }
  .modal-dialog--centered {
    min-height: calc(100% - 3.5rem); }
  .modal-dialog--centered::before {
    height: calc(100vh - 3.5rem);
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content; }
  .modal-dialog--sm {
    max-width: 400px; } }

@media (min-width: 992px) {
  .modal-dialog--lg,
  .modal-dialog--xl {
    max-width: 800px; } }

@media (min-width: 1200px) {
  .modal-dialog--xl {
    max-width: 1140px; } }



// The prefers-reduced-motion CSS media feature is used to detect if the user has requested that the system minimize the amount of non-essential motion it uses.
@media (prefers-reduced-motion: reduce) {
  .btn {
    transition: none; }
  .fade {
    transition: none; }
  .collapsing {
    transition: none; }
  .modal.fade .modal-dialog {
    transition: none; } }
